.slide-in-right-enter {
  transform: translateX(100%);
}
.slide-in-right-enter-active {
  transform: translateX(0%);
  transition: transform 0.5s ease-in-out;
}
.slide-in-right-exit {
  transform: translateX(0%);
}
.slide-in-right-exit-active {
  transform: translateX(-100%);
  transition: transform 0.5s ease-in-out;
}
