.preview-tab--1 {
  background: radial-gradient(
      89.69% 41.81% at 46.27% 49.01%,
      rgba(255, 255, 255, 0) 0%,
      rgba(5, 52, 49, 0.79) 100%
    ),
    url("../img/prev-1.jpg") no-repeat center center;
}
.preview-tab--2 {
  background: radial-gradient(
      89.69% 41.81% at 46.27% 49.01%,
      rgba(255, 255, 255, 0) 0%,
      rgba(5, 52, 49, 0.79) 100%
    ),
    url("../img/prev-2.jpg") no-repeat center center;
}
.preview-tab--3 {
  background: radial-gradient(
      89.69% 41.81% at 46.27% 49.01%,
      rgba(255, 255, 255, 0) 0%,
      rgba(5, 52, 49, 0.79) 100%
    ),
    url("../img/prev-3.jpg") no-repeat center center;
}
.preview-tab {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  overflow: hidden;
  z-index: 10;
  background-size: cover;
  padding: 3rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
}

.preview-tab .preview-tab__text {
  color: white;
  font: inherit;
  text-align: center;
  font-size: 1.4rem;

  cursor: default;
  -webkit-user-select: none; /* Chrome/Safari/Opera */
  -khtml-user-select: none; /* Konqueror */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none;
}

@media (max-width: 430px) {
  .preview-tab .preview-tab__text p {
    font-size: 1.3rem;
  }
}
@media (max-width: 410px) {
  .preview-tab .preview-tab__text p {
    font-size: 1.2rem;
  }
}
@media (max-width: 385px) {
  .preview-tab .preview-tab__text p {
    font-size: 1rem;
  }
}
.preview-tab .preview-tab__button {
  margin: 0 0 1rem 0;
  width: 100%;
}
.preview-tab .preview-tab__button--transparent {
  margin: 0;
  padding: 0;
  width: 100%;
}

.preview-tab .preview-tab__actions .preview-tab__elipses {
  height: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 0.5rem;
  cursor: initial;
}
.preview-tab .preview-tab__actions .preview-tab__elipses span.active {
  background: white;
}
.preview-tab .preview-tab__actions .preview-tab__elipses span {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.33);
  margin-right: 1rem;
}
