.mobile {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: center;
}
.block {
  background: #ffffff;
  width: 80%;
  margin: 30% 0;
  border-radius: 6px;
  padding: 1rem;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.15);
}

@media (min-width: 370px) {
  .block {
    margin: 20% 0;
    width: 70%;
  }
} ;
